slide[data-active] .slideContent{--x: calc(var(--px) - 0.5);--y: calc(var(--py) - 0.5);opacity:1;transform:perspective(1000px)}.slide[data-active] .slideContent:hover{transition:none;transform:perspective(1000px) rotateY(calc(var(--x) * 45deg)) rotateX(calc(var(--y) * -45deg))}@media(max-width: 768px){.slideContent{width:auto;height:90vh;margin-top:10%}}